/* @include responsive_table; */
@mixin responsive_table
{
	$row_bg_color:#e5e5e5;
	$row_bg_color_odd:#f0f0f0;
	$row_bg_color_higlight:#fff;
	$header_bg_color:#bfbfbf;
	$border_color:#777;
	$text_color:#333;
	.woob2b-resp-table {
		width: 100%;
		display: table;
		margin-bottom:30px;
		border-top: 1px solid $border_color;
		.woob2b-resp-table-body{
			display: table-row-group;
		}	
		.woob2b-resp-table-row{
			@include transitions(0.3s);
			@include turn_off_apearance;
			display: table-row;
			table-layout: fixed;
			background:$row_bg_color;
			color:$text_color;
			text-decoration: none !important;
			&*{
				text-decoration: none !important;
			}
			&:visited,&:active,&:focus{
				color:$text_color;
				text-decoration: none;
			}
			&:hover{
				color:$text_color;
				text-decoration: none;
				outline:none !important;
			}

			.woob2b-table-cell{
				display: table-cell;
				padding: 10px;
				vertical-align: middle;
				text-align:center;
				border-right: 1px solid $border_color;
				border-bottom: 1px solid $border_color;
				white-space: pre-wrap;
				word-break: keep-all;
				font-size:13px;
				&:nth-child(1){
					border-left: 1px solid $border_color;	
				}
				&.sm{
					max-width:110px;
				}

			}
			&:nth-child(odd){
				background:$row_bg_color_odd;
			}
			&:hover{
				background:$row_bg_color_higlight;
			}

			
		}

		.woob2b-resp-table-header .woob2b-table-cell{

			
			background:$header_bg_color;
			font-weight:bold;
			font-size:15px;
		}
	}
	.woob2b_actions{
		a{
			padding:0 4px;
		}
	}
	/*------------------------------ media ------------------------*/
	@media (max-width: 750px) {
		.woob2b-resp-table {
			.woob2b-resp-table-row{
				.woob2b-table-cell{
					&.inv-mob{
						display:none !important;
					}
				}
			}
		}
	}
}